<template>
	<div id="kaijiang-index">
		<div class="content bg-default font-14">
			<div class="count-down">
				<img src="static/img/kaijiang.jpg" alt="">
				<div class="days" v-if="xiaqi.starttime">距下次开奖时间：
					<count-down :endTime="xiaqi.starttime"></count-down>
				</div>
			</div>
			<div class="list-wrap" v-if="benqi && benqi.logs && benqi.logs.length > 0">
				<div class="list-title">
					<div class="font-18">本期中奖名单</div>
					<div class="font-14 margin-5-t">本期幸运日：{{benqi.rewarddate}}</div>
				</div>
				<div class="list-winner">
					<dl class="flex font-16">
						<dd>微信名称</dd>
						<dd>手机号码</dd>
					</dl>
					<div class="winner-inner">
						<dl class="flex" v-for="(item, index) in benqi.logs" :key="index">
							<dd class="ellipsis">{{item.nickname}}</dd>
							<dd class="ellipsis">{{item.mobile}}</dd>
						</dl>
					</div>
				</div>
			</div>
			<div class="list-wrap" v-if="shangqi && shangqi.logs && shangqi.logs.length > 0">
				<div class="list-title">
					<div class="font-18">上期中奖名单</div>
					<div class="font-14 margin-5-t">本期幸运日：{{shangqi.rewarddate}}</div>
				</div>
				<div class="list-winner">
					<dl class="flex font-16">
						<dd>微信名称</dd>
						<dd>手机号码</dd>
					</dl>
					<div class="winner-inner">
						<dl class="flex" v-for="(item, index) in shangqi.logs" :key="index">
							<dd class="ellipsis">{{item.nickname}}</dd>
							<dd class="ellipsis">{{item.mobile}}</dd>
						</dl>
					</div>
				</div>
			</div>
			<div class="list-wrap rules">
				<div class="list-title">
					<div class="font-18">活动规则</div>
				</div>
				<div class="list-winner">
					<div class="winner-inner" v-html="xiaqi.content">
						dddddddd
					</div>
				</div>
			</div>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import countDown from '@/components/countDown'
	export default {
		components: {
			countDown
		},
		data() {
			return {
				showPreLoading: true,
				benqi: {},
				shangqi: {},
				xiaqi: {
					content: '',
				},
			}
		},
		methods: {
			onLoad(){
				var that = this;
				that.util.request({
					url: 'kaijiang/index/index',
					data: {
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if (result.errno) {
						this.$toast(result.message)
						return;
					}
					result = result.message;
					that.benqi = result.benqi;
					that.xiaqi = that.util.extend(that.xiaqi, result.xiaqi);
					that.shangqi = result.shangqi;
					that.util.setWXTitle(that.benqi.title);
				});
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#kaijiang-index .count-down{
		position: relative;
		font-size: 0px;
		margin-bottom: 28px;
	}
	#kaijiang-index .count-down img{
		width: 100%;
		height: auto;
	}
	#kaijiang-index .count-down .days{
		position: absolute;
		top: 55.5%;
		left: 3%;
		right: 4%;
		bottom: 33%;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 24px;
		color: #fff;
	}
	#kaijiang-index .list-wrap{
		position: relative;
		height: 291px;
		margin: 0px 15px;
		border-radius: 10px;
		background-color: #FFB339;
		color: #fff;
		margin-bottom: 56px;
	}
	#kaijiang-index .list-wrap:last-child{
		margin-bottom: 15px;
	}
	#kaijiang-index .list-wrap .list-title{
		position: absolute;
		left: 20px;
		right: 20px;
		transform: translateY(-50%);
		height: 56px;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		border-radius: 200px;
		background-color: #EB6100;
	}
	#kaijiang-index .list-wrap .list-winner{
		position: absolute;
		top: 43px;
		left: 0px;
		right: 0px;
		bottom: 15px;
	}
	#kaijiang-index .list-wrap .list-winner .winner-inner{
		position: absolute;
		top: 31px;
		right: 0;
		bottom: 0;
		left: 0;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
		overflow-scrolling:touch;
	}
	#kaijiang-index .list-wrap .list-winner .winner-inner::-webkit-scrollbar {
		display: none;
	}
	#kaijiang-index .list-wrap .list-winner dl{
		padding-bottom: 15px;
	}
	#kaijiang-index .list-wrap .list-winner dl:last-child{
		padding-bottom: 0;
	}
	#kaijiang-index .list-wrap .list-winner dl dd{
		width: 50%;
		text-align: center;
	}
	#kaijiang-index .list-wrap .list-winner dl dd:first-child{
		padding: 0 10px 0 15px;
	}
	#kaijiang-index .list-wrap .list-winner dl dd:last-child{
		padding: 0 15px 0 10px;
	}
	#kaijiang-index .list-wrap.rules .list-winner .winner-inner{
		top: 0px;
		padding: 0 15px;
	}
</style>


